<template>
  <ul :class="customClass">
    <li v-for="(item, index) in months[ic8]"
      :class="{ 'active': index === value }" 
      @click="chooseMonth(index)">{{ item }}</li>
  </ul>
</template>

<script>
  import dayjs from 'dayjs'

  import { prefix } from '../../utils'

  const prefixClass = prefix + '-month'

  export default {
    name: prefixClass,
    props: {
      value: {
        type: [String, Number],
      },
    },
    data () {
      return {
        prefixClass,
        ic8: 'zh-CN',
        months: {
          'zh-CN': ['1 月', '2 月', '3 月', '4 月', '5 月', '6 月', '7 月', '8 月', '9 月', '10 月', '11 月', '12 月']
        }
      };
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        
        return customClass
      }
    },
    methods: {
      chooseMonth(month) {
        this.$emit('input', month)
        this.$emit('change', month)
      }
    }
  }
</script>